.visualize__page__container{
    height: 430px;
    width: 370px;
    background-color: #f7f9f9;
    margin: auto;
    padding: 20px;
}
.visualize__page__container h3{
    text-align: center;
}
.action__container{
    margin-top: 15px;
    display: flex;
    justify-content: space-around;
}
.action__container select{
    padding:4px;
    outline: none;
    border: none;
    font-weight: 600;
}
.visualize__page__container .barchart__container{
    height: 300px;
    width: 330px;
    margin: 20px auto;
    overflow: scroll;
}