.loginorsignup__page__container {
  height: fit-content;
  position: absolute;
  width: 300px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loginorsignup__page__container h2 {
  width: fit-content;
  margin: 20px auto;
}
.loginorsignup__form__container h3 {
  color: blueviolet;
}
.loginorsignup__page__container h2::after {
  content: "";
  height: 2px;
  width: 100%;
  display: block;
  background-color: #f2f3f4;
}
.loginorsignup__form__container {
  text-align: center;
  border: 2px solid #f2f3f4;
  padding: 5px 0px;
}
.loginorsignup__form__container img {
  height: 50px;
  width: 45px;
}
.loginorsignup__form__container form button {
  padding: 7px 30px;
  color: #fff;
  background-color: blueviolet;
  border-radius: 20px;
}
.input__container {
  width: 80%;
  margin: 10px auto;
  text-align: left;
}
.input__container input {
  padding: 7px 5px;
  width: 100%;
}
.terms__condition__container {
  display: flex;
  flex-direction: row-reverse;
  width: fit-content;
  margin: 10px auto;
}
.terms__condition__container input {
  margin-right: 5px;
}
.terms__condition__container input:focus {
  border: none;
  box-shadow: none;
}
.form__options {
  font-size: 14px;
  margin-top: 5px;
}
.form__options:hover {
  color: blueviolet;
  cursor: pointer;
}
