.income__expense__item {
  width: 100%;
  display: flex;
  background-color: #ecf0f1;
  justify-content: space-between;
  padding: 5px 10px;
  margin-top: 10px;
  border-radius: 15px;
  cursor: pointer;
}
.income__item__left {
  display: flex;
  align-items: center;
}
.income__item__left p {
  width: 150px;
  overflow: hidden;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.income__item__left .income__item__left__icon {
  height: 40px;
  width: 40px;
  background-color: blueviolet;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30%;
  margin-right: 10px;
}
.income__item__right {
  display: flex;
  justify-content: center;
  align-items: center;
}
h3 {
  font-size: 18px;
}
