.expensetype__container{
    display: flex;
    width: 95px;
    padding: 5px;
    align-items: center;
    background-color: #fff;
    border-radius: 10px;
    cursor: pointer;
    transition: border-radius .8s;
}
.expensetype__container:hover{
    background-color: rgb(205, 199, 240);
}
.expensetype__container span{
    margin-left: 5px;
    font-size: 12px;
    font-weight: 600;
}
.active{
    background-color: rgb(205, 199, 240);
}