.welcome__page__container {
  height: fit-content;
  width: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
h2 {
  text-align: center;
  color: blueviolet;
}
.animation__container {
  width: fit-content;
  margin: 30px auto;
  display: flex;
  height: 22px;
  overflow: hidden;
  font-weight: 700;
  font-size: 18px;
}
.animation__container .animation__text__container {
  margin-right: 5px;
  animation: animate-text 8s infinite;
  height: fit-content;
  color: orange;
}
.animation__text__container div {
  text-align: center;
}
@keyframes animate-text {
  0% {
    margin-top: 0px;
  }
  30% {
    margin-top: 0px;
  }
  33% {
    margin-top: -22px;
  }
  63% {
    margin-top: -22px;
  }
  66% {
    margin-top: -44px;
  }
  96% {
    margin-top: -44px;
  }
  100% {
    margin-top: -66px;
  }
}
.button__container {
  width: fit-content;
  margin: auto;
}
a {
  text-decoration: none;
}
.button__container button {
  outline: none;
  border: none;
  display: flex;
  font-weight: 700;
  font-size: 18px;
  justify-content: center;
  align-items: center;
  padding: 10px 30px;
  background-color: blueviolet;
  color: #fff;
  border-radius: 25px;
  cursor: pointer;
  transition: 0.4s;
  border: 1px solid grey;
}
.button__container button:hover {
  color: black;
  background-color: #fff;
  border: 1px solid grey;
}
.img__container {
  height: 20px;
  margin-left: 10px;
}
.button__container img {
  height: 20px;
}
