.addtransactionform__container {
  width: 370px;
  background-color: #f7f9f9;
  margin: 20px auto;
  padding: 10px 30px;
}
.addtransactionform__container h3 {
  text-align: center;
}
.row__1,
.row__2,
.row__3,
.row__4 {
  margin-top: 15px;
}
.row__1 {
  display: flex;
  justify-content: space-between;
}
.row__1 input {
  padding: 5px 0;
  width: 90px;
}
.row__2 input,
.row__3 input {
  width: 100%;
  padding: 5px 2px;
}
.addtransactionform__container label {
  font-size: 14px;
  color: grey;
}
.addtransactionform__container select {
  padding: 5px 2px;
  outline: none;
  border: none;
}
.row__4 h5 {
  text-align: center;
}
.row__4 .expense__types__list__container {
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}
.row__5{
    text-align: center;
}
.row__5 button {
  padding: 8px 10px;
  background-color: blueviolet;
  color: #fff;
  margin-top: 15px;
}
