.settingpage__container {
  width: 370px;
  height: fit-content;
  padding: 20px;
  background-color: #f7f9f9;
  margin: auto;
}
.settingpage__container h3 {
  text-align: center;
  color: blueviolet;
  margin-bottom: 20px;
}
.user__details {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  height: 70px;
  padding: 0 10px;
}
.user__details .user__details__left p{
    width: 180px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.user__details__left {
  display: flex;
  align-items: center;
}
.user__details .user__avatar{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin-right: 20px;
}
.user__details .user__avatar span{
  font-size: 10px;
}
.user__details .user__avatar .input__container{
  position: absolute;
  height: 50px;
  width: 50px;
  cursor: pointer;
}
.user__details .user__avatar .input__container input{
  height: 50px;
  width: 50px;
  background: transparent;
  opacity: 0;
}
.user__details .img__container {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 2px solid blueviolet;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.user__details .img__container img {
  height: 50px;
  width: 50px;
}
.user__details .edit__img__container img {
  height: 30px;
  width: 30px;
  cursor: pointer;
}
.user__settings {
  margin-top: 20px;
}
.row {
  display: flex;
  height: 30px;
  padding: 0 10px;
  background-color: #fff;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
.row__1 .select__currency {
  height: 25px;
  width: 70px;
  background-color: #ccc;
  display: flex;
  border-radius: 20px;
  cursor: pointer;
  position: relative;
  justify-content: center;
  align-items: center;
}
.row__1 .select__currency span {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  height: 100%;
  transition: background-color 0.4s;
}
.row__1 .select__currency .selected {
  background-color: blueviolet;
  color: #fff;
}
.row__3 {
  flex-direction: column;
  height: 70px;
  padding: 10px;
  justify-content: space-between;
}
.row__3 a {
  margin-left: 10px;
  cursor: pointer;
}
.row__4 {
  flex-direction: column;
  height: 70px;
  justify-content: space-around;
  padding: 5px;
}
.row__4 button {
  padding: 5px 10px;
  color: #fff;
  background-color: blueviolet;
  margin-top: 5px;
}
.row__5 {
  flex-direction: column;
  height: 70px;
  justify-content: space-around;
}
.row__5 button {
  padding: 5px;
  width: 310px;
  color: #fff;
}
.row__5 .btn__1 {
  background-color: red;
}
.row__5 .btn__2 {
  background-color: green;
}
.app__details p {
  font-size: 12px;
  text-align: center;
  margin-top: 10px;
}
