.transaction__details__container {
  width: 370px;
  margin: auto;
  height: fit-content;
  text-align: center;
  background-color: #f7f9f9;
  padding: 10px 0;
}
.row__1 h3 {
  color: grey;
}
.row__1 span {
  margin-top: 10px;
  font-size: 14px;
  color: grey;
}
.row__2 {
  margin-top: 30px;
}
.row__2 span {
  color: black;
  font-weight: 600;
  margin-top: 10px;
}
.row__2 p {
  width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 3px 10px;
  border-radius: 10px;
  margin: 10px auto;
  background-color: rgb(248, 201, 201);
  color: red;
  font-size: 12px;
  white-space: nowrap;
  font-weight: 600;
}
.row__2 .income__description{
  background-color: rgb(182, 228, 182);
  color: green
}
.row__3 {
  display: flex;
  width: 280px;
  margin: auto;
  margin-top: 20px;
  justify-content: space-around;
}
.row__3 button {
  padding: 10px 0;
  color: #fff;
  width: 120px;
}
.row__3 .btn__1 {
  background-color: orange;
}
.row__3 .btn__2 {
  background-color: blueviolet;
}
