.header__container {
  position: sticky;
  top: 0;
  z-index: 999;
  background-color: #fff;
}
.header__container .heading__container {
  height: 50px;
  width: 100%;
  background-color: #f7f9f9;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__container h3 {
  color: blueviolet;
  letter-spacing: 1px;
}
.header__container .nav__links__container {
  width: 370px;
  margin: 20px auto;
  font-size: 13px;
  background-color: #f7f9f9;
  padding: 30px;
}
.nav__links__container ul li {
  cursor: pointer;
  list-style: none;
}
.nav__links__container ul li a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-weight: 600;
}
.nav__links__container ul li a:hover {
  color: blueviolet;
}
img {
  height: 25px;
  width: 25px;
}

.nav__links__container ul {
  display: flex;
  justify-content: space-around;
}
.header__container .active {
  color: blueviolet;
}
