.dashboard__container {
  height: fit-content;
  width: 370px;
  background-color: #f7f9f9;
  margin: auto;
  padding: 10px;
  text-align: center;
}
.dashboard__container .total__balance__show__container {
  margin-top: 20px;
}
.dashboard__container .total__balance__show__container .total__balance__text {
  font-size: 12px;
  font-weight: 500;
}
.dashboard__container .total__balance__show__container .total__balance {
  margin-top: 5px;
  font-size: 18px;
  font-weight: 500;
}
.dashboard__container .income__expense__container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 25px;
}
.dashboard__container .income__expense__container .income__container,
.expense__container {
  width: fit-content;
  background-color: #ecf0f1;
  padding: 15px 25px;
  position: relative;
}
.dashboard__container .income__expense__container div p {
  font-weight: 500;
  font-size: 12px;
}
.dashboard__container .income__expense__container div span {
  font-weight: 500;
  font-size: 18px;
}
.dashboard__container .income__expense__container .arrow__image__container {
  position: absolute;
  top: 5px;
  right: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.arrow__image__container img {
  height: 13px;
  width: 18px;
}
