.income__expense__list__container {
  height: fit-content;
  width: 370px;
  margin: 20px auto;
  background-color: #f7f9f9;
  padding: 10px 10px;
  height: 300px;
  overflow: scroll;
}
.income__expense__list__container::-webkit-scrollbar {
  display: none;
}
.heading {
  display: flex;
  justify-content: space-between;
}
.heading span {
  font-size: 12px;
  background-color: #e8ebeb;
  padding: 2px 7px;
  border-radius: 10px;
  color: #6c6e6e;
}
.showdate{
    font-size: 14px;
    margin-top: 10px;
}
.income__expense__list__container .nothing__show__msg{
    margin-top: 80px;
    text-align: center;
    color: blueviolet;
}
.filter__btn__container{
  height: 60px;
  width: 200px;
  margin: 20px auto;
  text-align: center;
}
.filter__btn__container .filter__btn{
  padding:8px;
  border-radius: 20px;
  color: #fff;
  background-color: blueviolet;
}