.modal__container{
    position: fixed;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: fit-content;
    padding: 3px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2ECC71;
    border-radius: 20px;
    color: #fff;
    font-size: 12px;
    animation: animate .3s 1 ease forwards;
}
@keyframes animate {
    0%{
        bottom: -10px;
    }
    100%{
        bottom: 20px;
    }
}