.chart__container{
    height: 300px;
    width: 300px;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.chart__container .details__container{
    width: 100%;
    text-align: center;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-row-gap: 10px;
    grid-column-gap: 13px;
}
.chart__container .item__container{
    display: flex;
    align-items: center;
}
.chart__container .item__container div{
    height: 8px;
    width: 8px;
    border-radius: 50%;
    margin-right: 3px;
}
.chart__container span{
    font-size: 12px;
    font-weight: 600;
}